.App {
  text-align: center;
}
.black-nav {
  display: flex;
  background: black;
  width: 100%;
  color: #fff;
  padding-left: 20px;
}
div {
  box-sizing: border-box;
}
.button {
  border: 2px solid pink;
  border-radius: 14px;
  padding: 10px;
  margin: 10px;
  cursor: pointer;
  transition: all 0.2s;
}
.button:hover {
  background: rgb(202, 76, 97);
  color: #fff;
}
.button span {
  display: block;
  width: 100%;
}
.list {
  padding-left: 20px;
  text-align: left;
  border-bottom: 1px solid grey;
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}
.modal {
  margin:20px 0;
  padding: 20px;
  background: #eee;
  text-align: left;
}
.input{
  padding: 5px;
  margin: 10px;
}
@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
